body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body{
  background-color: #282c34;
  color: white;
}

.pub .image-gallery-slides{
    height: 700px !important;
}
.pub .image-gallery-slides:hover{
    cursor: pointer;
}
.pub .image-gallery-description{
    bottom: 165px !important;
}

.fullscreen .image-gallery-slides{
    height: 100% !important;
}
.fullscreen .image-gallery-description{
    margin:40px  !important;
}
.image-gallery-thumbnails-wrapper{
    display: none !important;
}
.image-gallery-custom{
    bottom: 0;  
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: transparent;
    border: 0;
    cursor: pointer;
    outline: none;
    position: absolute;
    z-index: 4;
    right: 80px !important;
}
.image-gallery-custom::before{
    content: "\F149";
    font-size: 1.7em;
    padding: 15px 20px;
    text-shadow: 0 1px 1px #1a1a1a;
    color: #fff;
    line-height: .7;
    transition: color .2s ease-out;
    display: inline-block;
    font-family: "Ionicons";
    font-style: normal;
    font-weight: normal;
    -webkit-font-feature-settings: normal;
            font-feature-settings: normal;
    font-variant: normal;
    text-transform: none;
    text-rendering: auto;
}
.image-gallery-custom:hover{
    color: #fff;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}
